import {
    auth,
    ref,
    database,
} from './firebase';
import createIframe from './createIframe';

window.sgliveSSO = {

    initializeClient(dashboardUrl, callback) {
        let user;
        let sid;
        const signOut = () => {
            if (auth().currentUser) {
                auth()
                    .signOut()
                    .then((data) => {
                        callback({
                            user: null,
                        });
                    })
                    .catch((error) => {
                        callback({
                            user: null,
                        });
                    });
            } else {
                callback({
                    user: null,
                });
            }
        };
        const contentWindow = createIframe(dashboardUrl);
        window.addEventListener('message', (event) => {
            const message = event.data;
            const doCallback = () => {
                if (user && sid) {
                    callback({
                        user,
                        sid,
                    });
                }
            };
            if (message.type === 'sglAccessToken') {
                const {
                    sglAccessToken,
                } = message.data;
                if (sglAccessToken) {
                    auth()
                        .signInWithCustomToken(sglAccessToken)
                        .then((data) => {
                            if (data.emailVerified) {
                                user = data;
                                doCallback();
                            } else {
                                signOut();
                            }
                        })
                        .catch((error) => {
                            signOut();
                        });
                } else {
                    signOut();
                }
            }
            if (message.type === 'sglSid') {
                sid = message.data.sid;
                doCallback();
            }
        });
    },

    getPage(params, callback) {
        const b64DecodeUnicode = (str) => {
            return decodeURIComponent(
                atob(str)
                    .split('')
                    .map((c) => {
                        return '%' + ('00' + c.charCodeAt(0).toString(16)).slice(-2);
                    })
                    .join('')
            );
        };
        const getPageId = (params) => {
            const rejectInfo = {
                msg: 'no-firebase-id',
            };
            return new Promise((resolve, reject) => {
                if (params.firebaseId) {
                    resolve(params.firebaseId);
                } else if (params.editorId) {
                    const allWebsitesRef = ref.child(`websites`);
                    allWebsitesRef
                        .once('value')
                        .then((snap) => {
                            let firebaseId;
                            snap.forEach((itemSnap) => {
                                const item = itemSnap.val();
                                if (item.editor && item.editor.idPage === params.editorId) {
                                    firebaseId = itemSnap.key;
                                    return true;
                                }
                            });
                            if (firebaseId) {
                                resolve(firebaseId);
                            } else {
                                reject(rejectInfo);
                            }
                        })
                        .catch(() => {
                            reject(rejectInfo);
                        });
                } else {
                    reject(rejectInfo);
                }
            });
        };
        const getPageData = (firebaseId) => {
            const rejectInfo = {
                msg: 'wrong-firebase-id',
            };
            return new Promise((resolve, reject) => {
                const websiteRef = ref.child(`websites/${firebaseId}`);
                websiteRef
                    .once('value')
                    .then((snap) => {
                        const val = snap.val();
                        if (val) {
                            val.key = firebaseId;
                            resolve(val);
                        } else {
                            reject(rejectInfo);
                        }
                    })
                    .catch((error) => {
                        reject(rejectInfo);
                    });
            });
        };
        const checkHasPrivileges = (website, uid) => {
            const rejectInfo = {
                msg: 'edit-forbidden',
            };
            const checkIsAdmin = () => {
                return new Promise((resolve, reject) => {
                    auth()
                        .currentUser
                        .getIdToken()
                        .then((idToken) => {
                            const payload = JSON.parse(b64DecodeUnicode(idToken.split('.')[1]));
                            if (!!payload['admin']) {
                                resolve();
                            } else {
                                reject();
                            }
                        })
                        .catch((error) => {
                            reject();
                        });
                });
            };
            return new Promise((resolve, reject) => {
                const hasPrivileges = () => (website.users && website.users[uid]);
                const isPublic = () => (!website.users || !Object.keys(website.users).length);
                if (hasPrivileges() || isPublic()) {
                    resolve(website);
                } else {
                    checkIsAdmin()
                        .then(() => {
                            resolve(website);
                        })
                        .catch(() => {
                            reject(rejectInfo);
                        });
                }
            });
        };

        const checkPresence = (websiteData, uid) => {
            const rejectInfo = {
                msg: 'website-edited',
            };
            return new Promise((resolve, reject) => {
                const pagePresenceRef = ref.child(`pagePresence/${websiteData.key}`);
                pagePresenceRef.transaction((currentPagePresence) => {
                    return currentPagePresence ? undefined : uid;
                }, (error, committed) => {
                    if (error || !committed) {
                        reject(rejectInfo);
                    } else {
                        resolve(websiteData);
                    }
                });
            });
        };

        const setPresence = (websiteData, uid) => {
            return new Promise((resolve, reject) => {
                const pageRefs = {
                    pagePresence: ref.child(`pagePresence/${websiteData.key}`),
                    userPagePresence: ref.child(`userPagePresence/${uid}/${params.sid}/${websiteData.key}`),
                };
                pageRefs.pagePresence
                    .onDisconnect()
                    .remove();
                pageRefs.userPagePresence
                    .onDisconnect()
                    .remove();
                pageRefs.userPagePresence
                    .set(true);
                resolve(websiteData);
            });
        };
        getPageId(params)
            .then((firebaseId) => {
                return getPageData(firebaseId);
            })
            .then((websiteData) => {
                return checkHasPrivileges(websiteData, params.uid);
            })
            .then((websiteData) => {
                return checkPresence(websiteData, params.uid);
            })
            .then((websiteData) => {
                return setPresence(websiteData, params.uid);
            })
            .then((websiteData) => {
                callback({
                    page: websiteData,
                });
            })
            .catch((reason) => {
                callback(reason);
            });
    },

};



